@import "./src/Common/Styles/Sass/variables.scss";

.skillsBlock {
    min-height: 100vh;
    background-color: $bgFirstColor;
}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;

}

.titleBlock {
    background-color: beige;
}

.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    box-sizing: border-box;
}

